

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.flex{
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.profileImg{
  border: 3px solid white;
  margin-left: 15px;
  margin-bottom: 15px;
}

.navbar{
  background-color: goldenrod !important;
  font-size: 17px !important;
  color: antiquewhite !important;
  margin-bottom: 10px;
}
.ui.item.menu .item {
  color: white;
}

.ui.segment:last-child{
  padding: 0px;
}

.ui.grid+.grid{
  margin: 10px;
}

.ui.grid{
  margin: 10px;
}
.itemCard{
  padding: 10px;
  margin: 15px;
  border: 2px solid lightgrey;
  border-radius: 15px;
  padding-bottom: 10px;
}

.ui.link.cards{
  margin: 15px;
  display: flex;
  justify-content: space-around;
}

.ui.celled.table tr td:first-child, .ui.celled.table tr th:first-child{
  font-size: 16px;
}

.ui.celled.table tr td, .ui.celled.table tr th{
  font-size: 16px;
}

.ui.table {
  margin: 30px;
  width: 97% !important;
  text-align: center !important;
}

.ui.horizontal.segments{
  height: 100px;
  margin: 20px 0px 0px 0px;
}


.ui.block.header{
  margin-left: 30px;
  margin-right: 30px;
}

.container{
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 400px;
}

.formContainer{
  display: flex;
  height: 860px;
  align-content: center;
  justify-content: center;
}

.ui.form{
  width: 40%;
  height: 400px;
  align-self: center;
}

